.single-feature-img {
    display: flex;
  
    img {
      margin: 30px 0;
      margin-bottom: 0;
      max-width: 100%;
      height: auto;
    //   filter: grayscale(50%);
    }
  }
  
  .single-terms {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: flex-start;
    margin-top: 20px;
  }
  
  .single-container {
    @extend .container;
  }
  
  .single-container-post {
    @extend .container;
    max-width: 900px;
  
    @include respond-to('x-large') {
      max-width: $max-width;
    //   display: grid;
    //   grid-template-columns: 1fr 15em;
    //   gap: 1rem;
    }
  }
  
  .single-post-contents {
    overflow: auto;
    // max-width: 900px;
  }
  
  .series {
    @include font-size('base');
    margin: 2rem 0;
  }
  
  .series-this-post {
    color: var(--color-primary);
    border: 1px solid var(--color-primary);
    border-radius: 5px;
    padding: 0.3rem;
    @include font-size('sm');
    font-weight: 500;
    margin-left: 10px;
  }
  
  .single-next-previous {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
    align-items: baseline;
  
    & > * {
      background-color: transparent;
      border: 1px solid var(--color-contrast-medium-low);
      border-radius: 12px;
      padding: 10px;
      @include font-size('base');
      max-width: 300px;
      
      &:hover {
        text-decoration: none;
        border: 1px solid var(--color-contrast-high);
      }
    }
  }
  
  .related-posts {
    @include font-size('base');
  }

  .feature-image-wrap {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  
  .feature-image-text {
    @include font-size('sm');
    font-family: "PT Serif";
    font-style: italic;
    text-align: right;
    margin-top: 0;
    color: var(--color-contrast-high);
  }